<template>
    <button v-on:click="sendCallback">
        {{this.text}}
    </button>
</template>

<script>
export default {
    name: "ButtonSimple",
    props: {
        text: String,
        value: String,
        callback: Function
    },
    methods: {
        sendCallback() {
            this.callback(this.value);
        }
    }
}
</script>

<style scoped>
    button {
        border: none;
        background: #eeeeeeaa;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 150px;
        height: 50px;
        border-radius: 5px;
    }
</style>